import playAnimationSequence from './animations'
import appConfig from './config'
import anime from 'animejs/lib/anime.es'


const listenForDeplyButtonClick = () => {
    document.querySelector('#deploy')
    .addEventListener('click',()=>{
        playAnimationSequence();
    })
}

const toggleLoadingScreen = () => {

    const timeline = anime.timeline({
        duration: 500,
        easing: 'easeInOutQuad',
    })

    timeline.add({
        targets: '.loader-container',
        opacity: 0,
    })

    timeline.add({
        targets: '.loader-container',
        translateX: '-100%',
    })
}

const makeRendererResponsive = () => {
    window.onresize = () => {
        appConfig.camera.cameraObject.aspect = window.innerWidth / window.innerHeight;
        appConfig.camera.cameraObject.updateProjectionMatrix();
        appConfig.renderer.rendererObject.setSize( window.innerWidth, window.innerHeight );
    }
}

export {listenForDeplyButtonClick, toggleLoadingScreen, makeRendererResponsive}