import appConfig from './config'
import loadModel from './loadModel'
import { addAmbientLight, addPointLight, addHemisphereLight } from './lights'
import {listenForDeplyButtonClick, makeRendererResponsive}  from './htmlHandlers'
import {makeStars} from './stars'
import {
    Scene,
    PerspectiveCamera,
    WebGLRenderer,
    Clock,
    TextureLoader,
    sRGBEncoding,
    LinearEncoding,
    NoToneMapping,
} from 'three'

import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls'


const init = () => {

    let clock = new Clock()
    appConfig.clock = clock

    //Create a scene
    const scene = new Scene()
    
    //Create camera
    const camera = new PerspectiveCamera(
        appConfig.camera.fov, 
        appConfig.window.aspectRatio, 
        appConfig.camera.near, 
        appConfig.camera.far
    )

    camera.position.x = appConfig.camera.initialPosition.x 
    camera.position.y = appConfig.camera.initialPosition.y 
    camera.position.z = appConfig.camera.initialPosition.z 
    
    //Create renderer and set initial settings
    const renderer = new WebGLRenderer()
    renderer.setSize(appConfig.renderer.width,appConfig.renderer.height)
    renderer.setClearColor(0x000000)
    renderer.setPixelRatio(appConfig.window.pixelRatio)
    renderer.outputEncoding = LinearEncoding
    renderer.toneMapping = NoToneMapping
    renderer.antialias = true;

    //Append the renderer to DOM element specified in config.js
    document.querySelector(
        appConfig.renderer.rendererDomElement
    ).appendChild( renderer.domElement )

    //Update the config.js with created elements
    appConfig.scene = scene
    appConfig.camera.cameraObject = camera
    appConfig.renderer.rendererObject = renderer

    makeStars(2000)

    loadModel(scene)

    addAmbientLight()
    addHemisphereLight()

    //Add camera controls
    let controls = new OrbitControls(camera, renderer.domElement)
    controls.maxZoom = 50
    controls.maxDistance = 150
    controls.enableDamping = true
    controls.dampingFactor= 0.12
    controls.saveState()

    appConfig.camera.controls = controls

    makeRendererResponsive()
    listenForDeplyButtonClick()
    
    animate()
}

const animate = () => {
    requestAnimationFrame(animate)
    
    appConfig.camera.controls.update()
    appConfig.mixer.update(appConfig.clock.getDelta())
    
    appConfig.renderer.rendererObject.render(
        appConfig.scene,
        appConfig.camera.cameraObject
    )
}



init()