import { AmbientLight, PointLight, HemisphereLight } from 'three'
import appConfig from './config'


const addAmbientLight = () => {
    const light = new AmbientLight(0xf4f4f4,0.5)

    appConfig.scene.add( light )
}


const addHemisphereLight = () => {
    const light = new HemisphereLight(0xffffff, 0xffffff,4)
    light.position.set( 0, 300, 0 )
    appConfig.scene.add( light )
}

const addPointLight = (color, intensity, distance, position) => {
    const light = new PointLight(color, intensity, distance, 2);
    light.position.set(position[0],position[1], position[2]);
    
    appConfig.scene.add( light )
}


export {addAmbientLight, addHemisphereLight, addPointLight}