import appConfig from './config'



const playAnimationSequence = () => {
    const animations = appConfig.animations;

    
    animations.leftAntennaClip.play()
    
    setTimeout(()=>{
        animations.leftAntennaClip.paused = true
        animations.leftSolarArrayClip.play()
        
        setTimeout(()=>{
            animations.rightAntennaClip.play()

            setTimeout(()=>{
                animations.rightAntennaClip.paused = true
                animations.rightSolarArrayClip.play()

                setTimeout(()=>{
                    animations.leftAntennaClip.paused = false

                    setTimeout(()=>{
                        animations.rightAntennaClip.paused = false
                    },3000)
                },2000)
            },4800)
        },3000)
    },4800)
}

export default playAnimationSequence