
import appConfig from './config'

import {
    Box3, 
    Vector3, 
    AnimationMixer, 
    LoopOnce,
    Mesh,
    TextureLoader,
    SphereGeometry,
    MeshBasicMaterial,
    PMREMGenerator,
    CubeTextureLoader,
    RGBFormat

} from 'three'

import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader'

import { toggleLoadingScreen } from './htmlHandlers'


const loadModel = (scene) => {
    const loader = new GLTFLoader()

    
    const textureLoader = new TextureLoader()
    const texture = textureLoader.load(
                            '../assets/textures/planet.png',
                            () => {
                                toggleLoadingScreen()
                            }
                        )
    texture.format = RGBFormat
    texture.needsUpdate = true
    const sphere = new SphereGeometry(300,60,40)
    sphere.scale(-1, 1, 1)
    
    const material = new MeshBasicMaterial({map:texture});

    const sphereMesh = new Mesh(sphere, material);
    sphereMesh.rotation.y = Math.PI/2
    appConfig.scene.add(sphereMesh)


    //environment map
    var r = "https://threejs.org/examples/textures/cube/Bridge2/";
	var urls = [ r + "posx.jpg", r + "negx.jpg",
							r + "posy.jpg", r + "negy.jpg",
							r + "posz.jpg", r + "negz.jpg" ];
							
	var textureCube = new CubeTextureLoader().load( urls );
	textureCube.format = RGBFormat;


    loader.load(
        appConfig.model.path, 
        gltf => {
            //position the model in the center of the scene
            const box = new Box3().setFromObject(gltf.scene)
            const center = new Vector3()

            box.getCenter( center )

            gltf.scene.position.sub( center )
            gltf.scene.rotation.y = 3*Math.PI/2


            gltf.scene.traverse(child => {
                if(child instanceof Mesh){
                    child.material.envMap = textureCube
                    child.material.envMapIntensity = 2
                }
            })

            //animations setup

            const mixer = new AnimationMixer(gltf.scene)

            mixer.addEventListener('loop', e => {
                console.log(e);
            })

            appConfig.mixer = mixer
            let clips = gltf.animations

            const leftAntennaClip = mixer.clipAction(clips[0])
            const rightAntennaClip = mixer.clipAction(clips[1])
            const leftSolarArrayClip = mixer.clipAction(clips[2])
            const rightSolarArrayClip = mixer.clipAction(clips[3])

            leftAntennaClip.loop = LoopOnce
            leftAntennaClip.clampWhenFinished = true
            rightAntennaClip.loop = LoopOnce
            rightAntennaClip.clampWhenFinished = true
            leftSolarArrayClip.loop = LoopOnce
            leftSolarArrayClip.clampWhenFinished = true
            rightSolarArrayClip.loop = LoopOnce
            rightSolarArrayClip.clampWhenFinished = true

            appConfig.animations.leftAntennaClip = leftAntennaClip
            appConfig.animations.rightAntennaClip = rightAntennaClip
            appConfig.animations.leftSolarArrayClip = leftSolarArrayClip
            appConfig.animations.rightSolarArrayClip = rightSolarArrayClip

            //add model to the scene
            scene.add(gltf.scene)
        }, 
        undefined,
        error => console.log(error)
    )
}


export default loadModel