import appConfig from './config'
import {SphereBufferGeometry, Mesh, InstancedMesh, MeshPhongMaterial, MathUtils} from 'three'

const makeStars = (starsQuantity) => {

    let starGeometry = new SphereBufferGeometry(0.5);
    let starMaterial = new MeshPhongMaterial({color:0xffffff, emissive:0xffffff})

    let starMesh = new InstancedMesh(starGeometry,starMaterial,starsQuantity);
    for(let i=0;i<starsQuantity;i++){
        
        let starMesh = new Mesh(starGeometry, starMaterial);
        let min = -360;
        let max = 360;
        let minRadius = 400;
        let maxRadius = 5000;
        let randomX = Math.random() * (max - min) + min;
        let randomY = Math.random() * (max - min) + min;
        let randomRadius = Math.random() * (maxRadius - minRadius) + minRadius;
        starMesh.position.setFromSphericalCoords(randomRadius, MathUtils.degToRad(randomX),MathUtils.degToRad(randomY));

        appConfig.scene.add(starMesh);
    }
}

export {makeStars}